<template>
  <section>
    <el-row>
      <el-col :span="8">
        <el-input v-model="search.searchText" suffix-icon="el-icon-search"
        @keyup.enter.native="getData()" clearable
        @blur="getDataBySearch()"
         placeholder="输入您想查询的汇总编码或名称"></el-input>
      </el-col>
      <el-col :span="15" align="right">
        <el-button @click="handelAddItem"
         size="small" type="primary" icon="el-icon-plus">新 增</el-button>
      </el-col>
      <el-col :span="24" style="margin-top: 16px">
        <el-table :data="tableData"
        @cell-dblclick="handelView"
         style="width: 100%;">
          <el-table-column
            prop="detailitemcode"
            label="明细编码"
            width="160">
          </el-table-column>
          <el-table-column
            prop="detailitemname" header-align="center" align="center"
            label="明细名称">
          </el-table-column>
          <el-table-column
            prop="pinyincode" header-align="center" align="center"
            label="拼音码">
          </el-table-column>
          <el-table-column header-align="center" width="120"
            label="操作">
            <template slot-scope="scope">
              <el-button-group>
                <span class="icon-button-img" @click="handelDelete(scope.row)" ><img src="../../../assets/imgs/delete.png"></span>
                <span class="icon-button-img" @click="handelUpdata(scope.row)"><img src="../../../assets/imgs/edit.png"></span>
              </el-button-group>
            </template>
          </el-table-column>
        </el-table>
      </el-col>
      <el-col :span="24" class="toolbar">
        <el-pagination background layout="total, prev, pager, next, jumper" @current-change="handelPageChange" :current-page="search.pageNumber" :page-size="search.pageSize" :total="total" style="float:right;">
        </el-pagination>
      </el-col>
    </el-row>
    <el-dialog :title="'明细项目 ' + title" center
    width="85%"
    @close="dialogClose"
    :visible.sync="dialog_item">
      <el-row>
        <el-form :model="formData" label-width="100px"
        :rules="rules" ref="ruleForm">
        <el-col :span="12">
          <el-form-item label="明细编码" prop="summarycode">
            <el-input v-model="formData.detailitemcode" :disabled="usable"></el-input>
          </el-form-item>
          <el-form-item label="拼音码" prop="">
            <el-input v-model="formData.pinyincode"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="12">
         <el-form-item label="明细名称" prop="detailitemname">
            <el-input v-model="formData.detailitemname"
            @blur="getPinCode"></el-input>
          </el-form-item>
          <el-form-item label="HIS编码" prop="hisdetailitemcode">
            <el-input v-model="formData.hisdetailitemcode"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="24">
          <el-form-item label="汇总项目" prop="summaryname">
            <el-input v-model="formData.summaryname" readonly>
              <el-button slot="append" icon="el-icon-search" @click="handelOpenDialog"></el-button>
            </el-input>
          </el-form-item>
          <el-form-item label="备注" prop="">
            <el-input type="textarea" :rows="2" placeholder="请输入内容"
            v-model="formData.remark">
            </el-input>
          </el-form-item>
        </el-col>
        </el-form>
      </el-row>
      <div class="yiliao">
        <div class="yiliao-top">
          <span> 所属医疗项目</span>
          <el-input suffix-icon="el-icon-search" v-model="yiliaoSearchText"
          placeholder="请输入汇总编码、名称、拼音码查询" style="width: 40%;"
          @keydown.native.enter="funGetMedicalCare"
          @blur="funGetMedicalCare"></el-input>
        </div>
        <el-row>
          <el-col :span="11">
            <el-table :data="yiliaoData"
              @selection-change="handleSelectionChange"
              @cell-dblclick="handelDBSelect"
              height="320"
              style="width: 100%;">
                <!-- <el-table-column
                  prop="medicalcarecode"
                  label="医疗项目编码"
                  width="60">
                </el-table-column> -->
                <el-table-column
                  prop="medicalcarename" header-align="center" align="center"
                  label="医疗项目名称(全部)">
                </el-table-column>
                <el-table-column
                  prop="pinyincode" header-align="center" align="center"
                  label="拼音码">
                </el-table-column>
              </el-table>
          </el-col>
          <el-col :offset="2" :span="11">
              <!-- @cell-dblclick="handelRemove" -->
            <el-table :data="yilaioSaveList"

              height="320"
              style="width: 100%;">
                <el-table-column
                  prop="medicalcarename" header-align="center" align="center"
                  label="医疗项目名称(已选)">
                </el-table-column>
                <el-table-column
                  prop="number" header-align="center" align="center" width="100"
                  label="数量">
                  <template slot-scope="scope">
                    <el-input v-model="scope.row.number"></el-input>
                  </template>
                </el-table-column>
                <el-table-column header-align="center" width="75"
                    label="操作">
                    <template slot-scope="scope">
                      <el-button-group>
                        <span class="icon-button-img" @click="handelRemove(scope.$index)" ><img src="../../../assets/imgs/delete.png"></span>
                      </el-button-group>
                    </template>
                  </el-table-column>
              </el-table>
          </el-col>
        </el-row>
      </div>
        <el-row>
          <el-col class="toolbar" align='center'>
            <el-button size="small" @click="dialog_item=false">取消</el-button>
            <el-button type="primary" size="small" v-if="buttonShow" @click="handelSubmit()">确定</el-button>
          </el-col>
        </el-row>
    </el-dialog>
          <el-dialog title="汇总项目"
      width="45%" center=""
      :visible.sync="dialog_huizong">
      <el-row>
        <el-col :offset="1" :span="22">
          <el-input suffix-icon="el-icon-search" v-model="huizongSearch"
          placeholder="请输入汇总编码、名称、拼音码查询" clearable
          @blur="getHuiZongData"></el-input>
        </el-col>
        <el-col style="margin-top: 22px;">
          <el-table @row-dblclick="handelSelectHuiZong"
              :data="huizongData" height="280"
              style="width: 100%">
              <el-table-column
                prop="summarycode"
                label="项目编码">
              </el-table-column>
              <el-table-column
                prop="summaryname"
                label="项目名称">
              </el-table-column>
              <el-table-column
                prop="pinyincode"
                label="拼音码">
              </el-table-column>
            </el-table>
        </el-col>
      </el-row>
      </el-dialog>
  </section>
</template>
<script>
import {
  updateDetailItem, // 修改药品信息
  getWesternDistionaryTable, // 西药列表
  getDetailItem, // 获取单位列表
  insertDetailItem, // 添加单位
  updateSummaryItem, // 更新单位
  getSummaryItem, // 获取汇总
  getMedicalCare, // 医疗项目
  deleteDetailItem, // 删除
  getFromDetailItemSubByDetailItemCode, // 明细
} from '../../../api'
import {convertPinyin} from '../../../util/getPinYin'
import {mapState} from 'vuex'
export default {
  computed:{
    ...mapState('database',{
      xx: state => state.xx,
    }),
    usable:function (){
      if(this.opType=='add'){
        return false
      }else{
        return true
      }
    }
  },
  data(){
    return{
      tableData: [], //表格数据
      dialog_item: false, // 弹窗显示
      opType: 'add',
      formData: {},
      dialog_huizong: false, // 汇总窗口
      tempData: '', // 保存修改
      yiliaoData: [], // 医疗数据
      total: 0, // 分页 总条数
      title: '',
      flagOptions: [ // 项目类别
        { detailitemflag: 0, label: '检查'},
        { detailitemflag: 1, label: '检验'},
      ],
      buttonShow: false, // 确定是否显示
      huizongSearch: '', // 搜索汇总
      huizongData: [], // 汇总数据
      yiliaoSearchText: '', // 医疗搜索
      yilaioSaveList: [], // 医疗待存数据
      search:{
        searchText: '',
        pageNumber: 1,
        pageSize: 10,
      },
      rules:{
        // summarycode: [{ required: true, message: '请输入明细编码', trigger: 'blur' }],
        // summaryname: [{ required: true, message: '请输入明细名称', trigger: 'change' }],
        // detailitemflag: [{ required: true, message: '请选择项目类别', trigger: 'blur' }],
        // hisdetailitemcode: [{ required: true, message: '请输入HIS编码', trigger: 'blur' }],
      }
    }
  },
  methods:{
    handelSubmit(){ // 确定操作
      if(this.opType=='add'){
        this.funCheckFormData()
      }else{
        this.handelUpdataDW()
      }
    },
    handelInsert(){ // 插入数据
      this.formData.detailitemname2 = this.formData.detailitemname
      this.formData.jcsjdetailItemSubs = this.yilaioSaveList
      insertDetailItem(this.formData).then(res=>{
        if(res.data.code=='0'){
          this.$message.success('保存成功！')
          this.dialog_item = false
          this.funGetAllData()
        }else{
          this.$message.error('保存失败！')
        }
      })
    },
    getPinCode(){ // 获取拼音码
     if(this.formData.detailitemname && this.opType=='add')
     this.formData.pinyincode = convertPinyin(this.formData.detailitemname)
    },
    funGetAllData(){ // 获取全部
      this.search.searchText = ''
      this.pageNumber = 1
      this.getData()
    },
    funCheckFormData(){ // 检查数据
      this.$refs.ruleForm.validate((valid) => {
        if(valid){
          this.handelInsert()
        }
      })
    },
    handelRemove(index){ // 移除数据
      this.yilaioSaveList.splice(index,1)
    },
    handelAddItem(){ // 添加
      this.title = '新增'
      this.opType = 'add'
      this.formData = {
        detailitemcode: '',
        detailitemname: '',
        pinyincode: '',
        hisdetailitemcode: '',
        detailitemflag: 1,
        summarycode: '',
        summaryname: '',
      }
      this.dialog_item = true
      this.buttonShow = true
    },
    handelDelete(row){ // 列表项删除
      this.$confirm('确定要执行删除操作？','操作提示',{
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(()=>{ // 删除确定
        deleteDetailItem({detailItemCode: row.detailitemcode}).then(res=>{
          if(res.data.code == '0'){
            this.$message.success("删除成功!")
            this.funGetAllData()
          }else{
            this.$message.error("删除失败!")
          }
        })
      }).catch(()=>{ // 删除取消

      })
    },
    handleSelectionChange(){ // 选择项目

    },
    handelDBSelect(row){ // 医疗项目选择添加
      let lock = false
      this.yilaioSaveList.forEach(element=>{
        if(element.medicalcarecode == row.medicalcarecode){
          lock = true
          return
        }
      })
      if(lock){
        this.$message.warning('项目已存在列表')
        return
      }
      this.yilaioSaveList.push({...row,number: 1,detailitemcode: this.formData.detailitemcode})
    },
    handelOpenDialog(){ // 打开汇总窗口
      this.dialog_huizong = true
      this.getHuiZongData()
    },
    getHuiZongData(){ // 获取汇总数据
      getSummaryItem({searchText: this.huizongSearch,pageSize: 9999}).then(res=>{
        if(res.data.code == '0'){
          this.huizongData = res.data.data.rows
        }
      })
    },
    handelSelectHuiZong(row){ // 选择汇总
      this.formData.summarycode = row.summarycode
      this.formData.summaryname = row.summaryname
      this.dialog_huizong = false
    },
    funGetMXData(code){ // 获取明细
      getFromDetailItemSubByDetailItemCode({
        detailItemCode: code}).then(res=>{
          if(res.data.code == '1' && res.data.data){
            this.yilaioSaveList = res.data.data
          }else{
            this.yilaioSaveList = []
          }
      })
    },
    handelView(row){ // 查看详情
      this.title = '查看'
      this.opType='view'
      this.formData = {...row}
      this.funGetMXData(row.detailitemcode)
      this.dialog_item = true
    },
    handelUpdata(row){ // 修改按钮
      this.title = '修改'
      this.opType='edit'
      this.tempData = row.unitname
      this.buttonShow = true
      this.dialog_item = true
      this.tempData = row.detailitemname
      this.formData = row
      this.funGetMXData(row.detailitemcode)
    },
    dialogClose(){ // 对话框关闭回调
      this.buttonShow = false
      this.formData = {} //  移除表单数据
      this.yilaioSaveList = []
      this.$nextTick(()=>{
        this.$refs.ruleForm.resetFields()
      })
    },
    getData(){ // 获取列表数据
      getDetailItem(this.search).then(res=>{
        if(res.data.code=="0"){
          this.tableData = res.data.data.rows
          this.total = res.data.data.total
        }else{
          this.tableData = []
          this.total = 0
        }
      })
    },
    funGetMedicalCare(){ // 获取医疗项目
      getMedicalCare({searchText: this.yiliaoSearchText, pageSize: 9}).then(res=>{
        if(res.data.code == '0'){
          this.yiliaoData = res.data.data.rows
        }else{
          this.yiliaoData = []
        }
      })
    },
    getDataBySearch(){ // 搜索框查询
        this.search.pageNumber = 1
        this.getData()
    },
    handelPageChange(page){ // 翻页
      this.search.pageNumber = page
      this.getData()
    },
    handelUpdataDW(){ // 更新数据
      this.formData.jcsjdetailItemSubs = this.yilaioSaveList
      this.formData.detailitemname2 = this.formData.detailitemname
      this.formData.detailitemname = this.tempData
      updateDetailItem(this.formData).then(res=>{
        if(res.data.code=='0'){
          this.$message.success('修改成功!')
          this.getData()
          this.dialog_item = false
        }else{
          let msg = ''
          if(res.data.msg)
            msg = res.data.msg
          this.$message.error('修改失败!' + msg)
        }
      })
    }
  },
  mounted(){
    this.getData() // 列表初始数据
    this.funGetMedicalCare() // 获取医疗项目
  }
}
</script>
<style scoped>
.yiliao{
  border: 1px solid #ccc;
}
.yiliao-top{
  display: flex;
  height: 60px;
  align-items: center;
}
.yiliao-top span{
  margin: 0 10px;
}
.yiliao-top .jine{
  width: 40%;
  padding-left: 28px;
}
</style>



